<template>
	<div class="hello">
		<div class="title">
			<h1>{{ name }}</h1>
			<h2>{{phone}}</h2>
			<h2>{{mail}}</h2>
		</div>
		<div class="education">
			<div v-for="item in education" :key="item.id">
				<div>{{item.time}}</div>
				<div>{{item.school}}</div>
				<div>{{item.college}}</div>
				<div>{{item.major}}</div>
				<div>{{item.direction}}</div>
			</div>
		</div>

</template>

<script>
	export default {
		name: 'resume',
		data() {
			return {
				name: "翁鸿铨",
				phone: 18959939718,
				mail: "chainhq@hotmail.com",
				education: [{
						time: "2012.09 - 2016.06",
						school: "福建农林大学",
						college: "机电工程学院",
						major: "机械设计制造及其自动化",
						direction: "",
					},
					{
						time: "目前(拟录取)",
						school: "中国科学技术大学",
						college: "先进技术研究院",
						major: "电子信息",
						direction: "软件工程方向",
					}
				]
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}
</style>



